.ec-variations__grid{margin-bottom:1rem}.ec-variations__grid--colors button{font-size:0}.ec-variations__option{min-height:2.3rem;min-width:2.3rem;border:2px solid var(--gray)}.ec-variations__option:hover{border-color:var(--secondary-light)}.ec-variations__option:not(:last-of-type){margin-right:.4rem}.ec-variations__option--selected:not(:disabled){border-color:var(--secondary)}.ec-variations__option:disabled{cursor:not-allowed;position:relative}.ec-variations__option:disabled:after{content:"x";text-align:center;font-size:10px;line-height:1;padding-top:1.5px;background:var(--danger);color:var(--danger-yiq);height:14px;width:14px;border-radius:50%;display:block;position:absolute;top:-3px;right:-3px}