.ec-gallery{width:100%;text-align:center;display:-webkit-box;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;flex-direction:row;position:relative;-webkit-box-pack:center;justify-content:center}@media (min-width:992px){.ec-gallery{margin-bottom:0}}.ec-gallery__thumbs{z-index:2;position:absolute;width:auto;padding:0;display:-webkit-inline-box;display:inline-flex;top:100%;margin:-35px 0 0}@media (min-width:992px){.ec-gallery__thumbs{position:static;display:block;width:90px;margin:0 1.5rem 0 0;left:auto;bottom:auto;overflow-y:auto}}.ec-gallery__item{opacity:.5;-webkit-transition:opacity .15s linear;transition:opacity .15s linear;cursor:pointer;margin-right:.75rem}.ec-gallery__item:last-of-type{margin-right:0}.ec-gallery__item:before{display:block;content:" ";height:10px;width:10px;background:var(--gray);border-radius:50%}.ec-gallery__item--selected{cursor:auto;opacity:1}.ec-gallery__item--selected:before{background:var(--secondary)}@media (min-width:992px){.ec-gallery__item{margin:0 0 .5rem}.ec-gallery__item:last-of-type{margin-bottom:0}.ec-gallery__item:before{display:none}}.ec-gallery__stage{width:400px;max-width:100%;overflow:hidden;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.ec-gallery__thumb,.ec-gallery__thumb img{display:none}@media (min-width:992px){.ec-gallery__thumb,.ec-gallery__thumb img{display:block;width:100%;height:auto}}.ec-gallery__open{display:none}@media (min-width:992px){.ec-gallery__open{font-weight:700;font-size:1.15rem;padding:0 .5rem;display:inline-block;margin-bottom:.5rem}}.ec-gallery__big-image,.ec-gallery__big-image img{max-width:100%;width:auto;height:auto;cursor:-webkit-zoom-in;cursor:zoom-in}.ec-gallery img{user-drag:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}